.character {
    margin-bottom: 2px;
    margin-right: 2px;
    position: relative;
}

.id {
    font-size: 10px;
}

.ar {}

.right {
    float: right;
    width: 300px;
    border: 3px solid #73AD21;
    padding: 10px;
}

.character-wrapper {
    display: flex;
}

span.character {
    position: relative;
}

.character>img {
    width: 40px;
    height: 40px;
}

.constellation {
    position: absolute;
    text-align: center;
    top: 0;
    right: 0;
    width: 18px;
    height: 18px;
    background: rgba(0, 0, 0, .5);
    border-radius: 0 4px 0 4px;
    color: #fff;
    line-height: 20px;
    padding-left: 3px;
    font-size: 10px;
    background: transparent url(../public/static/images/constellation_bg.png) no-repeat 50%/contain;
    font-family: "Tahoma Regular", "Tahoma Bold", sans-serif !important;
}

.grey {
    filter: grayscale(70%);
    background-color: rgba(128, 128, 128, 1);
}

.light {
    background-color: rgb(213 170 16);
}

.filter-icon {
    width: 28px;
}

span.country {
    text-transform: uppercase;
    background: #8f8a8a;
    color: white;
    font-size: 10px;
    /* padding: 1px !important; */
}

span.meta {
    display: flex;
    flex-wrap: wrap;
}

.header-left {
    top: -34px;
    left: -15px;
    width: 150px;
    height: 150px;
    position: absolute;
}

.header-right {
    top: -10px;
    right: -100px;
    width: 150px;
    height: 150px;
    position: absolute;
}

.guaranteed {
    background: #b7680794;
    color: #ffffff;
    font-size: 8px;
    vertical-align: super;
}

.not-guaranteed {
    background: #0000009e;
    color: white;
    font-size: 8px;
    vertical-align: super;
}

img.primogem {
    width: 24px;
    height: 24px;
}

div.primogem {
    font-size: 9px;
    text-align: center;
    top: 24px;
    position: absolute;
    background: #80808073;
    border-radius: 4px;
    padding: 0 1px 0 1px;
    left: 5px;
}

.header-container {
    position: relative;
    height: 100px
}

.pity-assurance-50 {
    font-size: 9px;
    background-color: rgba(136, 0, 0, 0.562);
    color: white;
    vertical-align: super;
}

.pity-assurance-100 {
    font-size: 9px;
    background-color: rgba(1, 119, 36, 0.486);
    color: white;
    vertical-align: super;
}

.pity-number {
    font-size: 20px;
}

#root {
    overflow: hidden;
}